import React from "react";
import { globalstate } from "../GlobalState/GlobalStateProvider";
import { Link } from "react-router-dom";
class Checkout extends React.Component {
  state = {};
  async componentDidMount() {
    await this.context.checkLoggedIn();
    await this.context.calculateTotal();
  }
  render() {
    return (
      <main>
        <header>
          <nav className="navbar-expand-md navbar-dark bg-dark Navbar">
            <h1 className="navbar-brand">React Shopping App V2</h1>
            <div className="container TogglerContainer">
              <button
                className="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarNavAltMarkup"
                aria-controls="navbarNavAltMarkup"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                  <Link className="nav-link me-lg-2" to="/ShppingAppV2">
                    Shopping
                  </Link>
                  <button
                    style={{ border: "none" }}
                    onClick={() => this.context.logoutOnClick()}
                    className="nav-link me-lg-2 bg-transparent"
                  >
                    Logout
                  </button>
                  <a className="nav-link" href="#">
                    Github
                  </a>
                </div>
              </div>
            </div>
          </nav>
        </header>
        <section>
          <div
            style={{
              width: "33%",
              display: this.context.SuccessToggle ? "block" : "none",
            }}
            className="alert alert-success alert-dismissible fade show ms-auto me-auto mt-5"
            role="alert"
          >
            <strong>Holy guacamole!</strong> You should check in on some of
            those fields below.
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
            ></button>
          </div>
          <form
            onSubmit={(e) => this.context.continueToShippingOnClick(e)}
            className="row g-3 container ms-auto me-auto mt-5"
          >
            <div className="col-md-6">
              <label htmlFor="inputFirstName" className="form-label">
                First name
              </label>
              <input
                type="text"
                className="form-control"
                id="inputFirstName"
                // required
              ></input>
            </div>
            <div className="col-md-6">
              <label htmlFor="inputLastName" className="form-label">
                Last name
              </label>
              <input
                type="text"
                className="form-control"
                id="inputLastName"
                // required
              ></input>
            </div>
            <div className="col-12">
              <label htmlFor="inputAddress" className="form-label">
                Address
              </label>
              <input
                type="text"
                className="form-control"
                id="inputAddress"
                placeholder="1234 Main St"
                // required
              ></input>
            </div>
            <div className="col-12">
              <label htmlFor="inputAddress2" className="form-label">
                Address 2
              </label>
              <input
                type="text"
                className="form-control"
                id="inputAddress2"
                placeholder="Apartment, studio, or floor"
                // required
              ></input>
            </div>
            <div className="col-md-6">
              <label htmlFor="inputCity" className="form-label">
                City
              </label>
              <input
                type="text"
                className="form-control"
                id="inputCity"
                // required
              ></input>
            </div>
            <div className="col-md-4">
              <label htmlFor="inputState" className="form-label">
                State
              </label>
              <select
                id="inputState"
                className="form-select"
                // required
              >
                <option hidden defaultValue>
                  Choose...
                </option>
                <option value="AL">Alabama</option>
                <option value="AK">Alaska</option>
                <option value="AZ">Arizona</option>
                <option value="AR">Arkansas</option>
                <option value="CA">California</option>
                <option value="CO">Colorado</option>
                <option value="CT">Connecticut</option>
                <option value="DE">Delaware</option>
                <option value="DC">District Of Columbia</option>
                <option value="FL">Florida</option>
                <option value="GA">Georgia</option>
                <option value="HI">Hawaii</option>
                <option value="ID">Idaho</option>
                <option value="IL">Illinois</option>
                <option value="IN">Indiana</option>
                <option value="IA">Iowa</option>
                <option value="KS">Kansas</option>
                <option value="KY">Kentucky</option>
                <option value="LA">Louisiana</option>
                <option value="ME">Maine</option>
                <option value="MD">Maryland</option>
                <option value="MA">Massachusetts</option>
                <option value="MI">Michigan</option>
                <option value="MN">Minnesota</option>
                <option value="MS">Mississippi</option>
                <option value="MO">Missouri</option>
                <option value="MT">Montana</option>
                <option value="NE">Nebraska</option>
                <option value="NV">Nevada</option>
                <option value="NH">New Hampshire</option>
                <option value="NJ">New Jersey</option>
                <option value="NM">New Mexico</option>
                <option value="NY">New York</option>
                <option value="NC">North Carolina</option>
                <option value="ND">North Dakota</option>
                <option value="OH">Ohio</option>
                <option value="OK">Oklahoma</option>
                <option value="OR">Oregon</option>
                <option value="PA">Pennsylvania</option>
                <option value="RI">Rhode Island</option>
                <option value="SC">South Carolina</option>
                <option value="SD">South Dakota</option>
                <option value="TN">Tennessee</option>
                <option value="TX">Texas</option>
                <option value="UT">Utah</option>
                <option value="VT">Vermont</option>
                <option value="VA">Virginia</option>
                <option value="WA">Washington</option>
                <option value="WV">West Virginia</option>
                <option value="WI">Wisconsin</option>
                <option value="WY">Wyoming</option>
              </select>
            </div>
            <div className="col-md-2">
              <label htmlFor="inputZip" className="form-label">
                Zip
              </label>
              <input
                type="text"
                className="form-control"
                id="inputZip"
                // required
              ></input>
            </div>
            <div className="col-12"></div>
            <div className="col-12 d-flex flex-column align-items-center">
              <div>
                <h2 style={{ display: "inline-block" }}>Your total is:</h2>{" "}
                <h2 style={{ display: "inline-block" }}>
                  {this.context.CartTotal}
                </h2>
              </div>
              <button
                style={{ width: "33%" }}
                type="submit"
                className="btn btn-primary btn-block"
              >
                Checkout
              </button>
            </div>
          </form>
        </section>
      </main>
    );
  }
}
Checkout.contextType = globalstate;
export default Checkout;



