import React from "react";
import { globalstate } from "../GlobalState/GlobalStateProvider";
import { Link } from "react-router-dom";
class Login extends React.Component {
  state = {};
  render() {
    return (
      <main>
        <header>
          <nav className="navbar-expand-md navbar-dark bg-dark Navbar">
            <h1 className="navbar-brand">React Shopping App V2</h1>
            <div className="container TogglerContainer">
              <button
                className="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarNavAltMarkup"
                aria-controls="navbarNavAltMarkup"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                  <Link to="/" className="nav-link" aria-current="page">
                    Shopping
                  </Link>
                  <Link className="nav-link" to="/CreateAccount">
                    Create Account
                  </Link>
                  <a className="nav-link" href="#">
                    Github
                  </a>
                </div>
              </div>
            </div>
          </nav>
        </header>
        <section>
          <div
            className="alert alert-danger alert-dismissible container mt-4"
            style={{
              width: "40%",
              display: this.context.ErrorToggle ? "flex" : "none",
            }}
            role="alert"
          >
            <strong>{this.context.ErrorMsg}</strong>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
            ></button>
          </div>
          <form className="LoginForm">
            <label htmlFor="Email">
              Email: Use demo account - TestUser@email.com
            </label>
            <input
              className="form-control form-control-lg"
              type="Email"
              value={this.context.LoginEmail}
              name="LoginEmail"
              placeholder="Email"
              required
              onChange={(e) => this.context.inputOnChange(e)}
            ></input>
            <label htmlFor="Password">
              Password: Use demo account - P@ssword123
            </label>
            <input
              className="form-control form-control-lg"
              type="Password"
              value={this.context.LoginPassword}
              name="LoginPassword"
              placeholder="Password"
              required
              onChange={(e) => this.context.inputOnChange(e)}
            ></input>
            <button
              style={{ display: this.context.LoginLoading ? "none" : "block" }}
              onClick={(e) => this.context.loginSubmit(e)}
              type="Submit"
              className="btn btn-block btn-success LoginBtn"
            >
              Login
            </button>
            <button
              style={{ display: this.context.LoginLoading ? "block" : "none" }}
              className="btn btn-block btn-success LoginBtn"
              type="button"
              disabled
            >
              <span
                className="spinner-border spinner-border-sm"
                role="status"
                aria-hidden="true"
              ></span>
              Loading...
            </button>
          </form>
        </section>
      </main>
    );
  }
}
Login.contextType = globalstate;
export default Login;



