import React from "react";
import { globalstate } from "../GlobalState/GlobalStateProvider";
import { Link } from "react-router-dom";

class CreateAccount extends React.Component {
  state = {};
  render() {
    return (
      <main>
        <header>
          <nav className="navbar-expand-md navbar-dark bg-dark Navbar">
            <h1 className="navbar-brand">React Shopping App V2</h1>
            <div className="container TogglerContainer">
              <button
                class="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarNavAltMarkup"
                aria-controls="navbarNavAltMarkup"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span class="navbar-toggler-icon"></span>
              </button>
              <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div class="navbar-nav">
                  <Link
                    to="/ShppingAppV2"
                    className="nav-link"
                    aria-current="page"
                  >
                    Shopping
                  </Link>
                  <Link to="/Login" className="nav-link" aria-current="page">
                    Login
                  </Link>
                  <a class="nav-link" href="#">
                    Github
                  </a>
                </div>
              </div>
            </div>
          </nav>
        </header>
        <section>
          <div
            className="alert alert-danger alert-dismissible container mt-4"
            style={{
              width: "40%",
              display: this.context.ErrorToggle ? "flex" : "none",
            }}
            role="alert"
          >
            <strong>{this.context.ErrorMsg}</strong>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
            ></button>
          </div>
          <div
            className="alert alert-success alert-dismissible container mt-4"
            style={{
              width: "40%",
              display: this.context.SuccessToggle ? "flex" : "none",
            }}
            role="alert"
          >
            <strong>{this.context.SuccessMsg}</strong>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
            ></button>
          </div>
          <form
            onSubmit={(e) => this.context.createAccountOnSubmit(e)}
            className="LoginForm"
          >
            <label htmlFor="Email">Email</label>
            <input
              className="form-control form-control-lg"
              type="Email"
              name="CreateEmail"
              value={this.context.CreateEmail}
              placeholder="Email"
              required
              onChange={(e) => this.context.inputOnChange(e)}
            ></input>
            <label htmlFor="Username">Username</label>
            <input
              className="form-control form-control-lg"
              type="text"
              value={this.context.CreateUsername}
              name="CreateUsername"
              placeholder="Username"
              required
              onChange={(e) => this.context.inputOnChange(e)}
            ></input>
            <label htmlFor="Password">Password</label>
            <input
              className="form-control form-control-lg"
              type="Password"
              value={this.context.CreatePassword}
              name="CreatePassword"
              placeholder="Password"
              required
              onChange={(e) => this.context.inputOnChange(e)}
            ></input>
            <ul className="PasswordUl">
              <li
                style={{
                  textDecoration: this.context.PasswordCap
                    ? "Line-through"
                    : "none",
                }}
              >
                Password has Capital letter
              </li>
              <li
                style={{
                  textDecoration: this.context.PasswordSpec
                    ? "Line-through"
                    : "none",
                }}
              >
                Password has special character (ex. !, @, #, $, %)
              </li>
              <li
                style={{
                  textDecoration: this.context.PasswordLeng
                    ? "Line-through"
                    : "none",
                }}
              >
                Password is at least 8 characters long
              </li>
              <li
                style={{
                  textDecoration: this.context.PasswordMatch
                    ? "Line-through"
                    : "none",
                }}
              >
                Password matches
              </li>
            </ul>
            <label htmlFor="ConfirmPassword">Confirm Password</label>
            <input
              className="form-control form-control-lg"
              type="Password"
              value={this.context.CreateConfirmPassword}
              name="CreateConfirmPassword"
              placeholder="Confirm Password"
              required
              onChange={(e) => this.context.inputOnChange(e)}
            ></input>
            <button
              style={{
                display: this.context.CreateAccountLoading ? "none" : "block",
              }}
              type="Submit"
              className="btn btn-block btn-success LoginBtn"
            >
              Create Account
            </button>
            <button
              style={{
                display: this.context.CreateAccountLoading ? "block" : "none",
              }}
              class="btn btn-block btn-success LoginBtn"
              type="button"
              disabled
            >
              <span
                class="spinner-border spinner-border-sm"
                role="status"
                aria-hidden="true"
              ></span>
              Loading...
            </button>
          </form>
        </section>
      </main>
    );
  }
}
CreateAccount.contextType = globalstate;
export default CreateAccount;



