import React from "react";
import { globalstate } from "../GlobalState/GlobalStateProvider";
import { Link } from "react-router-dom";
import MainImage from "../images/mainscreen.jpg";
import CartItem from "../images/stuff.jpg";
class HomePage extends React.Component {
  state = {
    AddCartItem: {
      _id: "",
      name: "",
      quantity: 1,
      description: "",
      price: "",
    },
  };
  async componentDidMount() {
    await this.context.getShoppingItems();
    await this.context.homepageButtonEnable();
  }
  addItemOnClick = async (id, name, desc, price) => {
    await this.setState({
      AddCartItem: {
        _id: id,
        name: name,
        quantity: 1,
        description: desc,
        price: price,
      },
    });
  };
  qtyOnClick = (name) => {
    if (name === "LessQty") {
      if (this.state.AddCartItem.Qty === 1) {
        return;
      } else {
        this.setState({
          AddCartItem: {
            _id: this.state.AddCartItem._id,
            name: this.state.AddCartItem.name,
            quantity: this.state.AddCartItem.quantity - 1,
            description: this.state.AddCartItem.description,
            price: this.state.AddCartItem.price,
          },
        });
      }
    } else {
      this.setState({
        AddCartItem: {
          _id: this.state.AddCartItem._id,
          name: this.state.AddCartItem.name,
          quantity: this.state.AddCartItem.quantity + 1,
          description: this.state.AddCartItem.description,
          price: this.state.AddCartItem.price,
        },
      });
    }
  };
  render() {
    return (
      <main>
        <header>
          <nav className="navbar-expand-md navbar-dark bg-dark Navbar fixed-top">
            <h1 className="navbar-brand">React Shopping App V2</h1>
            <div className="container TogglerContainer">
              <button
                className="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarNavAltMarkup"
                aria-controls="navbarNavAltMarkup"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                  {this.context.Userinfo.length > 0 ? (
                    <Link to="/Cart" className="nav-link" aria-current="page">
                      {this.context.Userinfo[0].Username}'s cart
                    </Link>
                  ) : (
                    <Link to="/Login" className="nav-link" aria-current="page">
                      Login
                    </Link>
                  )}
                  <Link
                    style={{
                      display:
                        this.context.Userinfo.length < 1 ? "inline" : "none",
                    }}
                    className="nav-link"
                    to="/CreateAccount"
                  >
                    Create Account
                  </Link>
                  <a className="nav-link" href="#">
                    Github
                  </a>
                </div>
              </div>
            </div>
          </nav>
          <form className="bg-dark SearchForm">
            <input
              className="form-control me-2 SearchBox"
              type="search"
              placeholder="Search"
              aria-label="Search"
            ></input>
            <button
              className="btn btn-outline-success SearchButton"
              type="submit"
            >
              Search
            </button>
          </form>
        </header>
        <section className="bg-dark MainScreenSection">
          <div className="container d-md-flex text-light">
            <div>
              Reprehenderit eu Lorem eu do voluptate irure mollit eiusmod tempor
              irure aute qui sit fugiat. Sunt amet deserunt aliqua eiusmod
              occaecat excepteur irure anim ipsum. Culpa pariatur sunt commodo
              ad laboris officia aliqua dolore sint minim amet
              cillum.Reprehenderit eu Lorem eu do voluptate irure mollit eiusmod
              tempor irure aute qui sit fugiat. Sunt amet deserunt aliqua
              eiusmod occaecat excepteur irure anim ipsum. Culpa pariatur sunt
              commodo ad laboris officia aliqua dolore sint minim amet cillum.
            </div>
            <img
              className="MainScreenImage img-fluid"
              src={MainImage}
              alt="Main Shopping Image"
            ></img>
          </div>
        </section>
        <section className="bg-primary SignUp">
          <h3 className="text-light">Sign up and start shopping today!</h3>
          <Link className="text-light btn btn-success" to="/CreateAccount">
            Sign Up
          </Link>
        </section>
        <section className="p-5">
          <div
            style={{ display: this.context.Loading ? "block" : "none" }}
            className="text-center"
          >
            <div className="spinner-border" role="status"></div>
          </div>
          <div
            style={{ display: this.context.Loading ? "none" : "block" }}
            className="container"
          >
            <div className="row text-center">
              {this.context.ShoppingItems.map((ele) => {
                return (
                  <div key={ele._id} className="col-md mb-3">
                    <div
                      className="card bg-dark text-light"
                      style={{ width: "18rem" }}
                    >
                      <img
                        src={CartItem}
                        className="card-img-top"
                        alt="Item"
                      ></img>
                      <div className="card-body">
                        <h5 className="card-title">{ele.name}</h5>
                        <p className="card-text">{ele.description}</p>
                        <h6 className="card-text">${ele.price}</h6>
                        {/* Button trigger modal */}
                        <button
                          type="button"
                          className={this.context.ButtonDisable}
                          data-bs-toggle="modal"
                          data-bs-target="#AddItemModal"
                          onClick={(id, name, desc, price) =>
                            this.addItemOnClick(
                              ele._id,
                              ele.name,
                              ele.description,
                              ele.price
                            )
                          }
                        >
                          Add to Cart
                        </button>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
          {/* <!-- Modal --> */}
          <div
            className="modal fade"
            id="AddItemModal"
            tabIndex="-1"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">
                    {this.state.AddCartItem.name}
                  </h5>
                  <button
                    type="button"
                    className="btn-close"
                    data-bs-dismiss="modal"
                    aria-label="Close"
                  ></button>
                </div>
                <div className="modal-body">
                  <div className="d-flex align-items-center">
                    <button
                      onClick={(e) => this.qtyOnClick("LessQty")}
                      className="btn  QtyBtnMin"
                    >
                      <h1>-</h1>
                    </button>
                    <span style={{ marginRight: "10px" }} className="fw-bold">
                      Qty:
                    </span>
                    <span className="fw-bold">
                      {this.state.AddCartItem.quantity}
                    </span>
                    <button
                      onClick={(e) => this.qtyOnClick("MoreQty")}
                      className="btn  QtyBtnPlus"
                    >
                      <h1>+</h1>
                    </button>
                  </div>
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-bs-dismiss="modal"
                  >
                    Cancel
                  </button>
                  <button
                    onClick={(id, name, desc, qty, price) =>
                      this.context.addToCartOnClick(
                        this.state.AddCartItem._id,
                        this.state.AddCartItem.name,
                        this.state.AddCartItem.description,
                        this.state.AddCartItem.quantity,
                        this.state.AddCartItem.price
                      )
                    }
                    type="button"
                    className="btn btn-primary"
                    data-bs-dismiss="modal"
                  >
                    Add to cart
                  </button>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* Footer */}
        <footer
          className="Footer bg-dark text-light justify-content-center"
          style={{ display: this.context.Loading ? "none" : "flex" }}
        >
          <h5
            style={{ display: this.context.Loading ? "none" : "block" }}
            className="me-5"
          >
            Scott Heizler
          </h5>
          <h5 style={{ display: this.context.Loading ? "none" : "block" }}>
            &copy;2021
          </h5>
        </footer>
      </main>
    );
  }
}
HomePage.contextType = globalstate;
export default HomePage;



