import React from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";

export const globalstate = React.createContext();

class GlobalStateProvider extends React.Component {
  state = {
    LoginEmail: "TestUser@email.com",
    LoginPassword: "P@ssword123",
    CreateEmail: "",
    CreatePassword: "",
    CreateUsername: "",
    CreateConfirmPassword: "",
    PasswordCap: false,
    PasswordSpec: false,
    PasswordLeng: false,
    PasswordMatch: false,
    Loading: false,
    ShoppingItems: [],
    Userinfo: [],
    NeedToLoginPrompt: true,
    ButtonDisable: "btn btn-primary disabled",
    ErrorMsg: "",
    ErrorToggle: false,
    LoginLoading: false,
    CreateAccountLoading: false,
    SuccessMsg: "",
    SuccessToggle: false,
    CartTotal: 0,
  };
  inputOnChange = async (e) => {
    await this.setState({
      ...this.state,
      [e.target.name]: e.target.value,
    });
    await this.checkPasswords();
  };
  checkPasswords = async () => {
    await this.passwordCapitalLetter();
    await this.passwordSpecialCharacter();
    await this.passwordLength();
    await this.passwordMatch();
  };
  passwordCapitalLetter = () => {
    let capRegex = /[A-Z]/;
    let capTest = capRegex.test(this.state.CreatePassword);
    this.setState({
      ...this.state,
      PasswordCap: capTest,
    });
  };
  passwordSpecialCharacter = () => {
    let specCharacterRegex = /(?=.*[!@#$%^&*])/;
    let specialCharacterTest = specCharacterRegex.test(
      this.state.CreatePassword
    );
    this.setState({
      ...this.state,
      PasswordSpec: specialCharacterTest,
    });
  };
  passwordLength = () => {
    if (this.state.CreatePassword.length >= 8) {
      this.setState({
        ...this.state,
        PasswordLeng: true,
      });
    } else {
      this.setState({
        ...this.state,
        PasswordLeng: false,
      });
    }
  };
  passwordMatch = () => {
    if (this.state.CreatePassword === this.state.CreateConfirmPassword) {
      this.setState({
        ...this.state,
        PasswordMatch: true,
      });
    } else {
      this.setState({
        ...this.state,
        PasswordMatch: false,
      });
    }
  };
  getShoppingItems = async () => {
    console.clear();
    await this.setState({
      ...this.state,
      Loading: true,
    });
    await axios
      .get("https://aqueous-sierra-57355.herokuapp.com/Shopping")
      .then((response) => {
        this.setState({
          ...this.state,
          ShoppingItems: response.data,
          Loading: false,
        });
      })
      .catch((err) => {
        console.log(err.response);
      });
  };
  loginSubmit = async (e) => {
    e.preventDefault();
    await this.setState({
      ...this.state,
      LoginLoading: true,
    });
    await axios
      .post("https://aqueous-sierra-57355.herokuapp.com/Login", {
        data: {
          Email: this.state.LoginEmail,
          Password: this.state.LoginPassword,
        },
      })
      .then(async (response) => {
        window.sessionStorage.setItem(
          "token",
          JSON.stringify(response.data.token)
        );
        window.sessionStorage.setItem(
          "CartItems",
          JSON.stringify(response.data.Items)
        );
        window.sessionStorage.setItem(
          "Username",
          JSON.stringify(response.data.Username)
        );
        await this.setState({
          ...this.state,
          LoginEmail: "TestUser@email.com",
          LoginPassword: "P@ssword123",
          Userinfo: [
            {
              Username: response.data.Username,
              CartItems: response.data.Items,
            },
          ],
          LoginLoading: false,
        });
        this.props.history.push("/ShoppingAppV2");
      })
      .catch((err) => {
        console.log(err.response);
        this.setState({
          ...this.state,
          ErrorMsg: err.response.data.msg,
          ErrorToggle: true,
          LoginLoading: false,
        });
      });
  };
  checkLoggedIn = async () => {
    console.clear();
    let token = window.sessionStorage.getItem("token");
    if (token === null || !token || token === undefined) {
      window.sessionStorage.clear();
      await this.setState({
        ...this.state,
        NeedToLoginPrompt: true,
        ButtonDisable: "btn btn-primary disabled",
        Userinfo: [],
      });
      await this.props.history.push("/Login");
    } else {
      let Username = JSON.parse(window.sessionStorage.getItem("Username"));
      let CartItems = JSON.parse(window.sessionStorage.getItem("CartItems"));
      this.setState({
        ...this.state,
        ButtonDisable: "btn btn-primary",
        Userinfo: [{ Username, CartItems }],
      });
    }
  };
  homepageButtonEnable = async () => {
    let token = window.sessionStorage.getItem("token");
    if (token === null || !token || token === undefined) {
      window.sessionStorage.clear();
      await this.setState({
        ...this.state,
        NeedToLoginPrompt: true,
        ButtonDisable: "btn btn-primary disabled",
        Userinfo: [],
      });
    } else {
      let Username = JSON.parse(window.sessionStorage.getItem("Username"));
      let CartItems = JSON.parse(window.sessionStorage.getItem("CartItems"));
      this.setState({
        ...this.state,
        ButtonDisable: "btn btn-primary",
        Userinfo: [{ Username, CartItems }],
      });
    }
  };
  logoutOnClick = async () => {
    await window.sessionStorage.clear();
    await this.setState({
      ...this.state,
      ErrorMsg: "",
      ErrorToggle: false,
    });
    this.props.history.push("/Login");
  };
  addToCartOnClick = async (id, name, desc, qty, price) => {
    let token = window.sessionStorage.getItem("token");
    if (!token || token === null || token === undefined) {
      this.setState({
        ...this.state,
        NeedToLoginPrompt: true,
      });
      this.props.history.push("/Login");
    } else {
      let getItems = JSON.parse(window.sessionStorage.getItem("CartItems"));
      let getToken = JSON.parse(window.sessionStorage.getItem("token"));
      let checkForItem = getItems.some((ele) => {
        if (id === ele._id) {
          return true;
        } else {
          return false;
        }
      });
      if (checkForItem) {
        let updatedItemList = getItems.map((ele) => {
          if (ele._id === id) {
            ele.quantity = ele.quantity += qty;
          }
          return ele;
        });
        this.setState({
          ...this.state,
          Userinfo: [
            {
              Username: this.state.Userinfo[0].Username,
              CartItems: updatedItemList,
            },
          ],
        });
        await axios
          .put("https://aqueous-sierra-57355.herokuapp.com/SaveItem", {
            headers: {
              "x-auth-token": getToken,
              "Content-Type": "application/json",
            },
            data: {
              Items: updatedItemList,
            },
          })
          .then((response) => {
            window.sessionStorage.setItem(
              "CartItems",
              JSON.stringify(updatedItemList)
            );
          })
          .catch((err) => {
            console.log(err.response);
          });
      } else {
        let newItem = {
          _id: id,
          name: name,
          description: desc,
          quantity: qty,
          price: price,
        };
        await this.setState({
          ...this.state,
          Userinfo: [
            {
              Username: this.state.Userinfo[0].Username,
              CartItems: [...this.state.Userinfo[0].CartItems, newItem],
            },
          ],
        });
        await axios
          .post("https://aqueous-sierra-57355.herokuapp.com/SaveItems", {
            headers: {
              "x-auth-token": getToken,
              "Content-Type": "application/json",
            },
            data: {
              Items: this.state.Userinfo[0].CartItems,
            },
          })
          .then((response) => {})
          .catch((err) => {
            console.log(err.response);
          });
        window.sessionStorage.setItem(
          "CartItems",
          JSON.stringify(this.state.Userinfo[0].CartItems)
        );
      }
    }
  };
  removeItemFromCartOnClick = async (id) => {
    let getItems = JSON.parse(window.sessionStorage.getItem("CartItems"));
    let token = JSON.parse(window.sessionStorage.getItem("token"));
    if (!token) {
      this.setState({
        ...this.state,
        NeedToLoginPrompt: true,
      });
      this.props.history.push("/Login");
    } else {
      let updatedCartItems = await getItems.filter((ele) => {
        if (ele._id !== id) {
          return ele;
        }
      });

      await axios
        .put("https://aqueous-sierra-57355.herokuapp.com/DeleteSingle", {
          headers: {
            "x-auth-token": token,
            "Content-Type": "application/json",
          },
          data: { Items: updatedCartItems },
        })
        .then((response) => {
          window.sessionStorage.setItem(
            "CartItems",
            JSON.stringify(updatedCartItems)
          );
          this.setState({
            ...this.state,
            Userinfo: [
              {
                Username: this.state.Userinfo[0].Username,
                CartItems: updatedCartItems,
              },
            ],
          });
        })
        .catch((err) => {
          console.log(err.response);
        });
    }
  };
  createAccountOnSubmit = async (e) => {
    e.preventDefault();
    await this.setState({
      ...this.state,
      CreateAccountLoading: true,
    });
    if (!this.state.PasswordCap) {
      this.setState({
        ...this.state,
        ErrorToggle: true,
        ErrorMsg: "Password needs capital letter",
        CreatePassword: "",
        CreateConfirmPassword: "",
        CreateAccountLoading: false,
        PasswordCap: false,
        PasswordLeng: false,
        PasswordSpec: false,
        PasswordMatch: false,
      });
    } else if (!this.state.PasswordSpec) {
      this.setState({
        ...this.state,
        ErrorToggle: true,
        ErrorMsg: "Password needs special character",
        CreatePassword: "",
        CreateConfirmPassword: "",
        CreateAccountLoading: false,
        PasswordCap: false,
        PasswordLeng: false,
        PasswordSpec: false,
        PasswordMatch: false,
      });
    } else if (!this.state.PasswordLeng) {
      this.setState({
        ...this.state,
        ErrorToggle: true,
        ErrorMsg: "Password needs to be at least 8 characters long",
        CreatePassword: "",
        CreateConfirmPassword: "",
        CreateAccountLoading: false,
        PasswordCap: false,
        PasswordLeng: false,
        PasswordSpec: false,
        PasswordMatch: false,
      });
    } else if (!this.state.PasswordMatch) {
      this.setState({
        ...this.state,
        ErrorToggle: true,
        ErrorMsg: "Passwords need to match",
        CreatePassword: "",
        CreateConfirmPassword: "",
        CreateAccountLoading: false,
        PasswordCap: false,
        PasswordLeng: false,
        PasswordSpec: false,
        PasswordMatch: false,
      });
    } else {
      await axios
        .post("https://aqueous-sierra-57355.herokuapp.com/CreateAccount", {
          data: {
            username: this.state.CreateUsername,
            email: this.state.CreateEmail,
            password: this.state.CreatePassword,
          },
        })
        .then((response) => {
          this.setState({
            ...this.state,
            CreateEmail: "",
            CreatePassword: "",
            CreateUsername: "",
            CreateConfirmPassword: "",
            ErrorMsg: "",
            ErrorToggle: false,
            SuccessMsg: response.data.msg,
            SuccessToggle: true,
            CreateAccountLoading: false,
            PasswordCap: false,
            PasswordLeng: false,
            PasswordSpec: false,
            PasswordMatch: false,
          });
          setTimeout(() => {
            this.setState({
              ...this.state,
              SuccessToggle: false,
              SuccessMsg: "",
            });
          }, 1500);
        })
        .catch((err) => {
          this.setState({
            ...this.state,
            ErrorToggle: true,
            ErrorMsg: err.response.data.msg,
            CreateAccountLoading: false,
            PasswordCap: false,
            PasswordLeng: false,
            PasswordSpec: false,
            PasswordMatch: false,
            CreateEmail: "",
            CreatePassword: "",
            CreateUsername: "",
            CreateConfirmPassword: "",
          });
          setTimeout(() => {
            this.setState({
              ...this.state,
              ErrorMsg: "",
              ErrorToggle: false,
            });
          }, 1500);
        });
    }
  };
  shoppingCartQtyOnClick = async (name, id) => {
    let getCartItems = await JSON.parse(
      window.sessionStorage.getItem("CartItems")
    );
    if (name === "LessQty") {
      let updatedCartItems = await getCartItems.map((ele) => {
        if (ele._id === id) {
          if (ele.quantity === 1) {
            ele.quantity = 1;
          } else {
            ele.quantity -= 1;
          }
        }
        return ele;
      });
      await this.setState({
        ...this.state,
        Userinfo: [
          {
            Username: this.state.Userinfo[0].Username,
            CartItems: updatedCartItems,
          },
        ],
      });
      await window.sessionStorage.setItem(
        "CartItems",
        JSON.stringify(updatedCartItems)
      );
    } else {
      let updatedCartItems = await getCartItems.map((ele) => {
        if (ele._id === id) {
          ele.quantity += 1;
        }
        return ele;
      });
      await this.setState({
        ...this.state,
        Userinfo: [
          {
            Username: this.state.Userinfo[0].Username,
            CartItems: updatedCartItems,
          },
        ],
      });
      await window.sessionStorage.setItem(
        "CartItems",
        JSON.stringify(updatedCartItems)
      );
    }
  };
  calculateTotal = async () => {
    // Calculate total
    let map = await this.state.Userinfo[0].CartItems.map((ele) => {
      let total = (ele.quantity *= ele.price);
      return total;
    });
    let total = await map.reduce((a, c) => a + c, 0);
    await this.setState({
      ...this.state,
      CartTotal: total,
    });
  };
  continueToShippingOnClick = async (e) => {
    e.preventDefault();
    let token = JSON.parse(window.sessionStorage.getItem("token"));
    await axios
      .delete("https://aqueous-sierra-57355.herokuapp.com/Cart/Delete", {
        headers: {
          "x-auth-token": token,
          "Content-Type": "application/json",
        },
      })
      .then(async (response) => {
        await window.sessionStorage.setItem(
          "CartItems",
          JSON.stringify(response.data.msg)
        );
        await this.setState({
          ...this.state,
          SuccessToggle: true,
          SuccessMsg: "Items successfully shipped!",
          Userinfo: [
            {
              Username: this.state.Userinfo[0].Username,
              CartItems: response.data.msg,
            },
          ],
        });
        await setTimeout(async () => {
          await this.setState({
            ...this.state,
            SuccessToggle: false,
            SuccessMsg: "",
          });
          await this.props.history.push("/ShoppingAppV2");
        }, 1200);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  render() {
    return (
      <globalstate.Provider
        value={{
          ...this.state,
          inputOnChange: this.inputOnChange,
          getShoppingItems: this.getShoppingItems,
          loginSubmit: this.loginSubmit,
          checkLoggedIn: this.checkLoggedIn,
          logoutOnClick: this.logoutOnClick,
          addToCartOnClick: this.addToCartOnClick,
          removeItemFromCartOnClick: this.removeItemFromCartOnClick,
          createAccountOnSubmit: this.createAccountOnSubmit,
          shoppingCartQtyOnClick: this.shoppingCartQtyOnClick,
          homepageButtonEnable: this.homepageButtonEnable,
          continueToShippingOnClick: this.continueToShippingOnClick,
          calculateTotal: this.calculateTotal,
        }}
      >
        {this.props.children}
      </globalstate.Provider>
    );
  }
}

export default withRouter(GlobalStateProvider);



