import React from "react";
import { globalstate } from "../GlobalState/GlobalStateProvider";
import { Link } from "react-router-dom";
import MainImage from "../images/mainscreen.jpg";
class ShoppingCart extends React.Component {
  state = {
    CartItems: [],
  };
  async componentDidMount() {
    await this.context.checkLoggedIn();
    await this.getCartItems();
  }
  getCartItems = async () => {
    let getCartItems = await JSON.parse(
      window.sessionStorage.getItem("CartItems")
    );
    if (getCartItems === null) {
      await this.setState({
        ...this.state,
        CartItems: [],
      });
    } else {
      await this.setState({
        ...this.state,
        CartItems: [...getCartItems],
      });
    }
  };
  componentWillUnmount() {
    this.setState = (state, callback) => {
      return;
    };
  }
  render() {
    return (
      <main>
        <header>
          <nav className="navbar-expand-md navbar-dark bg-dark Navbar">
            <h1 className="navbar-brand">React Shopping App V2</h1>
            <div className="container TogglerContainer">
              <button
                className="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarNavAltMarkup"
                aria-controls="navbarNavAltMarkup"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                  <Link className="nav-link me-lg-2" to="/ShppingAppV2">
                    Shopping
                  </Link>
                  <button
                    style={{ border: "none" }}
                    onClick={() => this.context.logoutOnClick()}
                    className="nav-link me-lg-2 bg-transparent"
                  >
                    Logout
                  </button>
                  <a className="nav-link" href="#">
                    Github
                  </a>
                </div>
              </div>
            </div>
          </nav>
        </header>
        <section>
          <div className="container mt-3">
            {this.state.CartItems.length < 1 ? (
              <h3 className="text-center">No items in cart</h3>
            ) : null}
            {this.state.CartItems.map((ele) => {
              return (
                <div key={ele._id} className="card">
                  <div className="card-body d-flex justify-content-between align-items-center bg-dark text-light">
                    <img
                      src={MainImage}
                      alt="CartImage"
                      className="card-img d-none d-lg-block"
                      style={{
                        height: "auto",
                        width: "100px",
                      }}
                    ></img>
                    <h5 className="card-title">{ele.name}</h5>
                    <p className="d-none d-lg-block">{ele.description}</p>
                    <p className="d-none d-lg-block">{ele.price}</p>
                    <div className="d-flex align-items-center">
                      <button
                        onClick={async (name, id) => {
                          await this.context.shoppingCartQtyOnClick(
                            "LessQty",
                            ele._id
                          );
                          await this.getCartItems();
                        }}
                        className="btn  QtyBtnMin"
                      >
                        <h1>-</h1>
                      </button>
                      <span style={{ marginRight: "10px" }} className="fw-bold">
                        Qty:
                      </span>
                      <span className="fw-bold">{ele.quantity}</span>
                      <button
                        onClick={async (name, id) => {
                          await this.context.shoppingCartQtyOnClick(
                            "MoreQty",
                            ele._id
                          );
                          await this.getCartItems();
                        }}
                        className="btn  QtyBtnPlus"
                      >
                        <h1>+</h1>
                      </button>
                    </div>
                    <button
                      className="btn btn-danger btn-block"
                      onClick={async (id) => {
                        await this.context.removeItemFromCartOnClick(ele._id);
                        await this.getCartItems();
                      }}
                    >
                      X
                    </button>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="d-flex justify-content-center mt-3">
            <Link className="btn btn-primary me-4" to="/">
              Back to Shopping
            </Link>
            <Link
              to="/Cart/Checkout"
              style={{ width: "10rem" }}
              className="btn btn-success"
            >
              Go to Checkout
            </Link>
          </div>
        </section>
      </main>
    );
  }
}
ShoppingCart.contextType = globalstate;
export default ShoppingCart;



